import UI from "./UI";
import Canvas from "../misc/Canvas";
import Board from './Board';
import Client from "./Client";
var canvas = new Canvas("BicBacBoe");
var board = createBoard();
var client = new Client();
var ui = new UI(board, canvas, client, update);
board.ui = ui;
client.ui = ui;
// Allows debugging in developer console
// window.board = board;
// window.client = client;
// window.ui = ui;
function createBoard() {
    var size = calcBoardSize();
    var board = new Board(canvas.getCenter(), size, 1, canvas.getElement());
    return board;
}
function calcBoardSize() {
    return canvas.getBoundingDimension();
}
// Game loop
function update() {
    canvas.clear();
    board.update();
}
client.socket.on('updateBoard', function (boardData) {
    ui.dimensionSlider.value = boardData.dimensions;
    ui.playAudio("bruh");
    board.loadBoard(boardData);
    update();
});
client.socket.on('updatePlayerMark', function (mark) {
    ui.playerMark = mark;
});
update();
